import $ from 'jquery'

function renderGallery(vm,selector='.c-article img'){
    $(selector).each((i,ele) => {
        // srclazyload
        vm.images.push($(ele).attr('src'))
        // 
        $(ele).on('click',(e) => {
            vm.gallery_index = i
        })
    })
}

export default renderGallery